import { ChangeDetectionStrategy, Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { distinctUntilChanged, takeUntil } from 'rxjs/operators';
import { MessagesClientInboxJourneyConfigService } from '../../messages-client-inbox-journey-config.service';

@Component({
  selector: 'bb-messages-client-inbox-thread',
  templateUrl: './messages-client-inbox-thread.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MessagesClientInboxThreadComponent implements OnInit, OnDestroy {
  private readonly conversationId$$ = new BehaviorSubject<string>('');

  /**
   * Selected conversation id stream
   */
  conversationId$: Observable<string> = this.conversationId$$.pipe(distinctUntilChanged());

  /**
   * Mailbox type title
   */
  mailboxTypeTitle: string;

  private readonly destroy$$ = new Subject<void>();

  constructor(
    public readonly config: MessagesClientInboxJourneyConfigService,
    private readonly route: ActivatedRoute,
    private readonly router: Router,
  ) {
    const routeConfig = this.route.snapshot.routeConfig;
    this.mailboxTypeTitle = routeConfig?.data?.mailboxTypeTitle;
  }

  ngOnInit() {
    this.route.params.pipe(takeUntil(this.destroy$$)).subscribe(({ id }: Params) => {
      this.conversationId$$.next(id || '');
    });
  }

  /**
   * Navigates back
   */
  navigateBack() {
    this.router.navigate([`../`], { relativeTo: this.route });
  }

  ngOnDestroy() {
    this.destroy$$.next();
  }
}
