import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Tab } from '@backbase/messages-ui-ang';
import { BehaviorSubject, Observable } from 'rxjs';
import { MessagesClientInboxJourneyConfigService } from '../../messages-client-inbox-journey-config.service';

@Component({
  selector: 'bb-messages-client-inbox-list',
  templateUrl: './messages-client-inbox-list.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MessagesClientInboxListComponent {
  private readonly mailboxType$$ = new BehaviorSubject<string | undefined>(undefined);
  /**
   * Mailbox type title
   */
  readonly mailboxType$: Observable<string | undefined> = this.mailboxType$$.asObservable();

  /**
   * List of shown tabs
   */
  tabs: Tab[] = [];

  constructor(
    public readonly config: MessagesClientInboxJourneyConfigService,
    private readonly route: ActivatedRoute,
    private readonly router: Router,
  ) {
    const routeConfig = this.route.snapshot.routeConfig;
    this.mailboxType$$.next(routeConfig?.data?.mailboxType);
  }

  /**
   * Open conversation thread
   */
  openConversation(id: string) {
    this.router.navigate(['../conversation', { id }], { relativeTo: this.route });
  }
}
