import { Inject, Injectable, InjectionToken, Optional, Provider } from '@angular/core';
import '@angular/localize/init';

export interface MessagesClientInboxJourneyConfiguration {
  headingTitle: string;
  headingType: string;
  headingWrapperClasses: string;
  buttonText: string;
  createMessageOpenEventName: string;
  createMessageClosedEventName: string;
  itemsPerPage: number;
  customerServiceTitle: string;
  hideAssignedToColumn: boolean;
  maxAttachmentSize: string;
  replyMessageMaxLength: string;
  maxSubjectLength: number;
  maxMessageLength: number;
  modalHeader: string;
  hideComposeButton: boolean;
}

export const MessagesClientInboxJourneyConfigurationToken = new InjectionToken<MessagesClientInboxJourneyConfiguration>(
  'MessagesClientInboxJourneyConfiguration injection token',
);

export const configDefaults: MessagesClientInboxJourneyConfiguration = {
  headingTitle: $localize`:Client Inbox Journey - Heading title@@messages-client-inbox-journey.heading-title:Messages`,
  headingType: 'h1',
  headingWrapperClasses: '',
  // eslint-disable-next-line max-len
  buttonText: $localize`:Client Inbox Journey - Compose button text@@messages-client-inbox-journey.compose-button-text:Compose`,
  createMessageOpenEventName: 'bb.event.messages.create.message.open',
  createMessageClosedEventName: 'bb.event.messages.create.message.close',
  itemsPerPage: 10,
  // eslint-disable-next-line max-len
  customerServiceTitle: $localize`:Client Inbox Journey - Customer Service title@@messages-client-inbox-journey.customer-service-title:Customer Service`,
  hideAssignedToColumn: false,
  maxAttachmentSize: '10',
  replyMessageMaxLength: '300',
  maxSubjectLength: 100,
  maxMessageLength: 300,
  // eslint-disable-next-line max-len
  modalHeader: $localize`:Client Inbox Journey - Create new message modal header@@messages-client-inbox-journey.create-new-message-modal-header:New message`,
  hideComposeButton: true,
};

export const MessagesClientInboxJourneyConfigurationProvider: Provider = {
  provide: MessagesClientInboxJourneyConfigurationToken,
  useValue: configDefaults as Partial<MessagesClientInboxJourneyConfiguration>,
};

@Injectable()
export class MessagesClientInboxJourneyConfigService {
  private readonly _config: MessagesClientInboxJourneyConfiguration;
  constructor(
    @Optional()
    @Inject(MessagesClientInboxJourneyConfigurationToken)
    staticConfig?: MessagesClientInboxJourneyConfiguration,
  ) {
    this._config = { ...configDefaults, ...staticConfig };
  }

  get headingTitle(): string {
    return this._config.headingTitle;
  }

  get headingType(): string {
    return this._config.headingType;
  }

  get headingWrapperClasses(): string {
    return this._config.headingWrapperClasses;
  }

  get buttonText(): string {
    return this._config.buttonText;
  }

  get createMessageOpenEventName(): string {
    return this._config.createMessageOpenEventName;
  }

  get createMessageClosedEventName(): string {
    return this._config.createMessageClosedEventName;
  }

  get itemsPerPage(): number {
    return this._config.itemsPerPage;
  }

  get customerServiceTitle(): string {
    return this._config.customerServiceTitle;
  }

  get hideAssignedToColumn(): boolean {
    return this._config.hideAssignedToColumn;
  }

  get maxAttachmentSize(): string {
    return this._config.maxAttachmentSize;
  }

  get replyMessageMaxLength(): string {
    return this._config.replyMessageMaxLength;
  }

  get maxSubjectLength(): number {
    return this._config.maxSubjectLength;
  }

  get maxMessageLength(): number {
    return this._config.maxMessageLength;
  }

  get modalHeader(): string {
    return this._config.modalHeader;
  }

  get hideComposeButton(): boolean {
    return this._config.hideComposeButton;
  }
}
