import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MessagesClientInboxJourneyComponent } from './messages-client-inbox-journey.component';
import { provideRoutes, Route, RouterModule } from '@angular/router';
import { BackbaseCoreModule, TemplateRegistry } from '@backbase/foundation-ang/core';
import { MessagesCreateMessageModalWidgetModule } from '@backbase/messages-create-message-modal-widget-ang';
import { MessagesClientInboxListComponent } from './components/messages-client-inbox-list/messages-client-inbox-list.component';
import { MessagesConversationsListWidgetModule } from '@backbase/messages-conversations-list-widget-ang';
import { MessagesConversationThreadWidgetModule } from '@backbase/messages-conversation-thread-widget-ang';
import { MessagesClientInboxThreadComponent } from './components/messages-client-inbox-thread/messages-client-inbox-thread.component';
import { MessagesManageConversationsComponent } from './components/messages-manage-conversations/messages-manage-conversations.component';
import { defaultRoute } from './messages-client-inbox-journey.routes';
import { MessagesClientInboxJourneyConfigService } from './messages-client-inbox-journey-config.service';
import { MessagesCollectionUiAngModule } from '@backbase/messages-ui-ang';

const components = [
  MessagesClientInboxJourneyComponent,
  MessagesClientInboxListComponent,
  MessagesClientInboxThreadComponent,
  MessagesManageConversationsComponent,
];
@NgModule({
  declarations: [...components],
  imports: [
    CommonModule,
    RouterModule,
    MessagesCreateMessageModalWidgetModule,
    MessagesConversationsListWidgetModule,
    MessagesConversationThreadWidgetModule,
    MessagesCollectionUiAngModule,
    BackbaseCoreModule.withConfig({
      classMap: {
        MessagesClientInboxJourneyComponent,
      },
    }),
  ],
  providers: [MessagesClientInboxJourneyConfigService, TemplateRegistry],
})
export class MessagesClientInboxJourneyModule {
  static forRoot(
    data: { routes: Route; [key: string]: any } = { routes: defaultRoute },
  ): ModuleWithProviders<MessagesClientInboxJourneyModule> {
    return {
      ngModule: MessagesClientInboxJourneyModule,
      providers: [provideRoutes([data.routes])],
    };
  }
}
